<template>
  <div>
    <el-table :data="users" border>
      <el-table-column prop="username" label=""></el-table-column>
      <el-table-column prop="realName" label=""></el-table-column>
      <el-table-column prop="studentNumber" label="" v-if="userType === 'STUDENT'"></el-table-column>
      <el-table-column prop="email" label=""></el-table-column>
      <el-table-column prop="phone" label=""></el-table-column>
      <el-table-column label="" width="200">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
      @current-change="handlePageChange"
      :current-page.sync="currentPage"
      :page-size="pageSize"
      layout="total, prev, pager, next"
      :total="total">
    </el-pagination>
  </div>
</template>

<script>
export default {
  data() {
    return {
      users: [],
      userType: '', // 'STUDENT' or 'TEACHER'
      currentPage: 1,
      pageSize: 10,
      total: 0
    }
  },
  methods: {
    async fetchUsers() {
      try {
        const endpoint = this.userType === 'STUDENT' ? '/students' : '/teachers';
        const response = await this.$axios.get(`/api/admin/users${endpoint}`, {
          params: {
            pageNum: this.currentPage,
            pageSize: this.pageSize
          }
        });
        if (response.data.code === 200) {
          this.users = response.data.data.content;
          this.total = response.data.data.totalElements;
        } else {
          this.$message.error(response.data.msg || '');
        }
      } catch (error) {
        this.$message.error('' + error.message);
      }
    },
    handlePageChange(page) {
      this.currentPage = page;
      this.fetchUsers();
    },
    handleEdit(row) {
      this.$emit('edit', row);
    },
    handleDelete(row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        try {
          const response = await this.$axios.delete(`/api/admin/users/${row.id}`);
          if (response.data.code === 200) {
            this.$message.success('');
            this.fetchUsers();
          } else {
            this.$message.error(response.data.msg || '');
          }
        } catch (error) {
          this.$message.error('' + error.message);
        }
      }).catch(() => {});
    }
  },
  created() {
    this.fetchUsers();
  },
  watch: {
    userType: {
      handler(newVal) {
        if (newVal) {
          this.currentPage = 1;
          this.fetchUsers();
        }
      },
      immediate: true
    }
  }
}
</script> 