<template>
  <div class="user-form">
    <el-form :model="form" :rules="rules" ref="form" label-width="100px">
      <el-form-item label="" prop="username" v-if="!isEdit">
        <el-input v-model="form.username" placeholder=""></el-input>
      </el-form-item>
      
      <el-form-item label="" prop="userType">
        <el-select v-model="form.userType" placeholder="">
          <el-option label="" value="STUDENT"></el-option>
          <el-option label="" value="TEACHER"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="" prop="realName">
        <el-input v-model="form.realName" placeholder=""></el-input>
      </el-form-item>

      <!--  -->
      <el-form-item v-if="userType === 'STUDENT'" label="" prop="studentNumber">
        <el-input v-model="form.studentNumber" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="" prop="email">
        <el-input v-model="form.email" placeholder=""></el-input>
      </el-form-item>

      <el-form-item label="" prop="phone">
        <el-input v-model="form.phone" placeholder=""></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm"></el-button>
        <el-button @click="resetForm"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'UserForm',
  props: {
    userType: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      isEdit: false,
      form: {
        id: null,
        username: '',
        userType: '',
        realName: '',
        studentNumber: '',
        email: '',
        phone: ''
      },
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' }
        ],
        userType: [
          { required: true, message: '', trigger: 'change' }
        ],
        realName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        studentNumber: [
          { required: true, message: '', trigger: 'blur' }
        ],
        email: [
          { type: 'email', message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    setFormData(user) {
      this.isEdit = true
      this.form = { ...user }
    },
    submitForm() {
      this.$refs.form.validate(async (valid) => {
        if (valid) {
          try {
            const url = this.isEdit ? 
              `/api/admin/users/${this.form.id}` : 
              '/api/admin/users'
            const method = this.isEdit ? 'put' : 'post'
            
            this.form.userType = this.userType
            const response = await this.$axios[method](url, this.form)
            
            if (response.data.code === 200) {
              this.$message.success('')
              this.$emit('saved')
              this.resetForm()
            } else {
              this.$message.error(response.data.msg || '')
            }
          } catch (error) {
            this.$message.error('' + error.message)
          }
        }
      })
    },
    resetForm() {
      this.$refs.form.resetFields()
      this.isEdit = false
      this.form = {
        id: null,
        username: '',
        userType: '',
        realName: '',
        studentNumber: '',
        email: '',
        phone: ''
      }
    }
  },
  watch: {
    'form.userType'(newVal) {
      // 
      if (newVal !== 'STUDENT') {
        this.form.studentNumber = '';
      }
    }
  }
}
</script>

<style scoped>
.user-form {
  max-width: 600px;
  margin: 20px auto;
}
</style>