<template>
  <div class="teacher-management">
    <div class="filter-section">
      <el-form :inline="true" :model="filterForm">
        <el-form-item>
          <el-input
            v-model="filterForm.keyword"
            placeholder=""
            clearable
            @clear="handleSearch">
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSearch"></el-button>
          <el-button type="success" @click="handleAdd"></el-button>
          
        </el-form-item>
      </el-form>
    </div>

    <el-table :data="teachers" border v-loading="loading">
      <el-table-column prop="username" label=""></el-table-column>
      <el-table-column prop="realName" label=""></el-table-column>
      <el-table-column prop="email" label=""></el-table-column>
      <el-table-column prop="phone" label=""></el-table-column>
      <el-table-column label="" width="200">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
      @current-change="handlePageChange"
      :current-page.sync="currentPage"
      :page-size="pageSize"
      layout="total, prev, pager, next"
      :total="total">
    </el-pagination>

    <!-- / -->
    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible">
      <user-form
        ref="userForm"
        :user-type="'TEACHER'"
        @saved="handleSaved">
      </user-form>
    </el-dialog>

    <!-- Excel -->
    <el-dialog title="" :visible.sync="importDialogVisible" width="500px">
      <el-upload
        class="upload-demo"
        drag
        action="/api/admin/users/teacher/import"
        :headers="headers"
        :on-success="handleUploadSuccess"
        :on-error="handleUploadError"
        :before-upload="beforeUpload"
        :limit="1"
        accept=".xlsx">
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <div class="el-upload__tip" slot="tip">
          <p>xlsx10MB</p>
          <p><a href="javascript:;" @click="downloadTemplate"></a></p>
        </div>
      </el-upload>
      <div class="import-result" v-if="importResult.total">
        <p></p>
        <p>{{ importResult.total }}</p>
        <p>{{ importResult.success }}</p>
        <p>{{ importResult.failed }}</p>
        <div v-if="importResult.errors.length">
          <p></p>
          <ul>
            <li v-for="(error, index) in importResult.errors" :key="index">{{ error }}</li>
          </ul>
        </div>
      </div>
    </el-dialog>

    <!--  -->
    <div class="batch-import">
      <el-button type="primary" @click="importDialogVisible = true">
        <i class="el-icon-upload2"></i> 
      </el-button>
    </div>
  </div>
</template>

<script>
import UserForm from './UserForm.vue'

export default {
  name: 'TeacherManagement',
  components: {
    UserForm
  },
  data() {
    return {
      teachers: [],
      loading: false,
      currentPage: 1,
      pageSize: 10,
      total: 0,
      filterForm: {
        keyword: ''
      },
      dialogVisible: false,
      importDialogVisible: false,
      dialogTitle: '',
      currentUser: null,
      headers: {
        'X-Token': localStorage.getItem('token')
      },
      importResult: {
        total: 0,
        success: 0,
        failed: 0,
        errors: []
      }
    }
  },
  methods: {
    async fetchTeachers() {
      try {
        this.loading = true
        const response = await this.$axios.get('/api/users/teachers', {
          params: {
            pageNum: this.currentPage,
            pageSize: this.pageSize,
            keyword: this.filterForm.keyword
          }
        })
        if (response.data.code === 200) {
          this.teachers = response.data.data.content
          this.total = response.data.data.totalElements
        } else {
          this.$message.error(response.data.msg || '')
        }
      } catch (error) {
        this.$message.error(error.response?.data?.msg || error.message || '')
      } finally {
        this.loading = false
      }
    },
    handleSearch() {
      this.currentPage = 1
      this.fetchTeachers()
    },
    handlePageChange(page) {
      this.currentPage = page
      this.fetchTeachers()
    },
    handleAdd() {
      this.dialogTitle = ''
      this.currentUser = null
      this.dialogVisible = true
    },
    handleEdit(row) {
      this.dialogTitle = ''
      this.currentUser = row
      this.dialogVisible = true
      this.$nextTick(() => {
        this.$refs.userForm.setFormData(row)
      })
    },
    async handleDelete(row) {
      try {
        await this.$confirm('?', '', {
          type: 'warning'
        })
        const response = await this.$axios.delete(`/api/users/${row.id}`)
        if (response.data.code === 200) {
          this.$message.success('')
          this.fetchTeachers()
        } else {
          this.$message.error(response.data.msg || '')
        }
      } catch (error) {
        if (error !== 'cancel') {
          this.$message.error('' + error.message)
        }
      }
    },
    handleSaved() {
      this.dialogVisible = false
      this.fetchTeachers()
    },
    beforeUpload(file) {
      const isExcel = file.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
      const isLt10M = file.size / 1024 / 1024 < 10

      if (!isExcel) {
        this.$message.error(' xlsx !')
        return false
      }
      if (!isLt10M) {
        this.$message.error(' 10MB!')
        return false
      }
      return true
    },
    handleUploadSuccess(response) {
      if (response.code === 200) {
        this.importResult = {
          total: response.data.length,
          success: response.data.length,
          failed: 0,
          errors: []
        }
        this.$message.success('')
        this.fetchTeachers()
      } else {
        this.importResult = {
          total: 0,
          success: 0,
          failed: 1,
          errors: [response.message]
        }
        this.$message.error(response.message)
      }
    },
    handleUploadError(err) {
      this.importResult = {
        total: 0,
        success: 0,
        failed: 1,
        errors: [err.message || '']
      }
      this.$message.error('')
    },
    downloadTemplate() {
      // 
      const link = document.createElement('a')
      link.href = '/templates/teacher_import_template.xlsx' // 
      link.download = '.xlsx'
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
    }
  },
  created() {
    this.fetchTeachers()
  }
}
</script>

<style scoped>
.teacher-management {
  padding: 20px;
}
.filter-section {
  margin-bottom: 20px;
}
.batch-import {
  margin-top: 20px;
}
.import-result {
  margin-top: 20px;
  padding: 10px;
  background-color: #f5f7fa;
  border-radius: 4px;
}
.el-upload__tip {
  line-height: 1.5;
}
</style> 