<template>
  <div class="data-review">
    <div class="header">
      <h2></h2>
      <div class="actions">
        <el-button type="success" @click="exportData">
          <i class="el-icon-download"></i> 
        </el-button>
      </div>
    </div>

    <div class="filters">
      <el-form :inline="true" class="filter-form">
        <el-form-item label="">
          <el-select v-model="filters.status" placeholder="" clearable>
            <el-option label="" value="PENDING" />
            <el-option label="" value="APPROVED" />
            <el-option label="" value="REJECTED" />
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="filters.keyword"
            placeholder="/"
            clearable
            @keyup.enter.native="handleSearch"
          />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSearch"></el-button>
          <el-button @click="resetFilters"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <el-table
      v-loading="loading"
      :data="records"
      border
      style="width: 100%"
    >
      <el-table-column label="" type="index" width="50" align="center" />
      <el-table-column prop="studentNumber" label="" width="120" />
      <el-table-column prop="studentName" label="" width="120" />
      <el-table-column prop="className" label="" width="120" />
      <el-table-column label="" width="120">
        <template slot-scope="scope">
          {{ scope.row.sportsItem ? scope.row.sportsItem.name : '' }}
        </template>
      </el-table-column>
      <el-table-column prop="score" label="" width="100" />
      <el-table-column prop="reviewStatus" label="" width="100">
        <template slot-scope="scope">
          <el-tag :type="getReviewStatusType(scope.row.reviewStatus)">
            {{ getReviewStatusText(scope.row.reviewStatus) }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="reviewComment" label="" />
      <el-table-column prop="createdAt" label="" width="180">
        <template slot-scope="scope">
          {{ formatDateTime(scope.row.createdAt) }}
        </template>
      </el-table-column>
      <el-table-column label="" width="200" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleDetail(scope.row)"
          ></el-button>
          <el-button
            size="mini"
            type="success"
            @click="handleReview(scope.row, 'APPROVED')"
            v-if="scope.row.reviewStatus === 'PENDING'"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <div class="pagination-container">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addDialog.visible"
      width="500px"
      @closed="resetForm('addForm')"
    >
      <el-form :model="addForm" :rules="rules" ref="addForm" label-width="100px">
        <el-form-item label="" prop="studentId" required>
          <el-select
            v-model="addForm.studentId"
            filterable
            remote
            reserve-keyword
            placeholder=""
            :remote-method="searchStudents"
            :loading="studentLoading"
            style="width: 100%"
          >
            <el-option
              v-for="item in students"
              :key="item.id"
              :label="`${item.realName} (${item.studentNumber})`"
              :value="item.id"
            >
              <span>{{ item.realName }}</span>
              <span style="float: right; color: #8492a6; font-size: 13px">{{ item.studentNumber }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        
        <el-form-item label="" prop="sportsItemId" required>
          <el-select
            v-model="addForm.sportsItemId"
            filterable
            placeholder=""
            style="width: 100%"
            @change="handleSportsItemChange"
          >
            <el-option
              v-for="item in sportsItems"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        
        <el-form-item label="" prop="score" required>
          <el-input-number
            v-model="addForm.score"
            :precision="2"
            :step="0.01"
            :min="0"
            controls-position="right"
            style="width: 100%"
          ></el-input-number>
        </el-form-item>
        
        <el-form-item label="" prop="className">
          <el-select v-model="addForm.className" placeholder="" style="width: 100%">
            <el-option
              v-for="className in classNames"
              :key="className"
              :label="className"
              :value="className"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addDialog.visible = false"> </el-button>
        <el-button type="primary" @click="submitAdd"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog 
      title="" 
      :visible.sync="reviewDialog.visible" 
      width="500px"
    >
      <div class="review-info" v-if="currentRecord">
        <el-descriptions :column="2" border>
          <el-descriptions-item label="">{{ currentRecord.student ? currentRecord.student.realName : '-' }} ({{ currentRecord.studentNumber || '-' }})</el-descriptions-item>
          <el-descriptions-item label="">{{ currentRecord.sportsItem ? currentRecord.sportsItem.name : '-' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ currentRecord.score || '-' }} {{ currentRecord.sportsItem ? currentRecord.sportsItem.unit : '' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ currentRecord.className || '-' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ currentRecord.teacher ? currentRecord.teacher.realName : '-' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ formatDateTime(currentRecord.createdAt) }}</el-descriptions-item>
        </el-descriptions>
      </div>
      <el-form :model="reviewForm" ref="reviewForm" :rules="reviewRules" class="review-form">
        <el-form-item label="" prop="status">
          <el-radio-group v-model="reviewForm.status">
            <el-radio label="APPROVED"></el-radio>
            <el-radio label="REJECTED"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="comment">
          <el-input 
            type="textarea" 
            v-model="reviewForm.comment"
            :rows="3"
            placeholder="">
          </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="reviewDialog.visible = false"></el-button>
        <el-button type="primary" @click="submitReview"></el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="detailDialog.visible"
      width="500px"
    >
      <div class="record-detail" v-if="currentRecord">
        <el-descriptions :column="2" border>
          <el-descriptions-item label="">{{ currentRecord.student ? currentRecord.student.realName : '-' }} ({{ currentRecord.studentNumber || '-' }})</el-descriptions-item>
          <el-descriptions-item label="">{{ currentRecord.sportsItem ? currentRecord.sportsItem.name : '-' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ currentRecord.score || '-' }} {{ currentRecord.sportsItem ? currentRecord.sportsItem.unit : '' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ currentRecord.className || '-' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ currentRecord.teacher ? currentRecord.teacher.realName : '-' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ formatDateTime(currentRecord.createdAt) }}</el-descriptions-item>
          <el-descriptions-item label="">
            <el-tag :type="getReviewStatusType(currentRecord.reviewStatus)">
              {{ getReviewStatusText(currentRecord.reviewStatus) }}
            </el-tag>
          </el-descriptions-item>
          <el-descriptions-item label="">{{ currentRecord.reviewComment || '-' }}</el-descriptions-item>
        </el-descriptions>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="detailDialog.visible = false"></el-button>
        <el-button 
          type="primary" 
          @click="handleReview(currentRecord, 'APPROVED')"
          v-if="currentRecord && currentRecord.reviewStatus === 'PENDING'"
        ></el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog 
      title="" 
      :visible.sync="modifyDialog.visible" 
      width="400px"
    >
      <div class="review-info">
        <p>{{ currentRecord ? currentRecord.student ? currentRecord.student.realName : '-' : '-' }} ({{ currentRecord ? currentRecord.studentNumber : '-' }})</p>
        <p>{{ currentRecord ? currentRecord.sportsItem ? currentRecord.sportsItem.name : '-' : '-' }}</p>
        <p>{{ currentRecord ? currentRecord.score : '-' }} {{ currentRecord ? currentRecord.sportsItem ? currentRecord.sportsItem.unit : '' : '' }}</p>
      </div>
      <el-form :model="modifyForm" :rules="modifyRules" ref="modifyForm" label-width="80px">
        <el-form-item label="" prop="status">
          <el-select v-model="modifyForm.status" placeholder="">
            <el-option label="" value="PENDING"></el-option>
            <el-option label="" value="APPROVED"></el-option>
            <el-option label="" value="REJECTED"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="comment">
          <el-input
            type="textarea"
            v-model="modifyForm.comment"
            :rows="3"
            placeholder=""
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="modifyDialog.visible = false"> </el-button>
        <el-button type="primary" @click="submitModify"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import axios from 'axios'
import { 
  getTestRecords, 
  addTestRecord, 
  reviewTestRecord,
  getHistoryRecords,
  exportRecords,
  downloadTemplate,
  modifyReview
} from '@/api/testRecord'

export default {
  name: 'DataReview',
  data() {
    return {
      loading: false,
      records: [],
      total: 0,
      currentPage: 1,
      pageSize: 10,
      filters: {
        status: '',
        keyword: ''
      },
      sportsItems: [],
      classList: [],
      addDialog: {
        visible: false
      },
      addForm: {
        studentId: null,
        sportsItemId: null,
        score: 0,
        className: ''
      },
      rules: {
        studentId: [
          { required: true, message: '', trigger: 'change' }
        ],
        sportsItemId: [
          { required: true, message: '', trigger: 'change' }
        ],
        score: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      teachers: [],
      selectedItemUnit: '',
      students: [],
      reviewDialog: {
        visible: false
      },
      currentRecord: null,
      reviewForm: {
        id: null,
        status: '',
        comment: ''
      },
      reviewRules: {
        status: [{ required: true, message: '', trigger: 'change' }],
        comment: [{ required: true, message: '', trigger: 'blur' }]
      },
      studentLoading: false,
      detailDialog: {
        visible: false
      },
      historyRecords: [],
      modifyDialog: {
        visible: false
      },
      modifyForm: {
        id: null,
        status: '',
        comment: ''
      },
      modifyRules: {
        status: [{ required: true, message: '', trigger: 'change' }],
        comment: [{ required: true, message: '', trigger: 'blur' }]
      },
      classNames: [],
    }
  },
  created() {
    this.init()
  },
  methods: {
    async init() {
      await Promise.all([
        this.getSportsItems(),
        this.fetchRecords()
      ])
    },
    async fetchRecords() {
      try {
        this.loading = true;
        const { status, keyword } = this.filters;
        const response = await this.$http.get('/api/admin/test-records', {
          params: {
            status,
            keyword,
            page: this.currentPage - 1,
            size: this.pageSize
          }
        });
        
        if (response.data.code === 200) {
          const { content, totalElements } = response.data.data;
          this.records = content;
          this.total = totalElements;
        } else {
          this.$message.error(response.data.msg || '');
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      } finally {
        this.loading = false;
      }
    },
    async getSportsItems() {
      try {
        const response = await this.$http.get('/api/sports-items')
        if (response.data.code === 200) {
          this.sportsItems = response.data.data.content || []
        } else {
          this.$message.error(response.data.msg || '')
        }
      } catch (error) {
        console.error(':', error)
        this.$message.error('')
      }
    },
    handleSearch() {
      this.currentPage = 1
      this.fetchRecords()
    },
    resetFilters() {
      this.filters = {
        status: '',
        keyword: ''
      };
      this.handleSearch(); // 
    },
    getReviewStatusText(reviewStatus) {
      const statusMap = {
        'PENDING': '',
        'APPROVED': '',
        'REJECTED': ''
      };
      return statusMap[reviewStatus] || reviewStatus;
    },
    getReviewStatusType(reviewStatus) {
      const typeMap = {
        'PENDING': 'warning',
        'APPROVED': 'success',
        'REJECTED': 'danger'
      };
      return typeMap[reviewStatus] || 'info';
    },
    formatDateTime(datetime) {
      if (!datetime) return ''
      return new Date(datetime).toLocaleString()
    },
    showAddDialog() {
      this.addDialog.visible = true;
      this.loadSportsItems();
      this.loadClassNames();
    },
    async exportData() {
      try {
        const loading = this.$loading({
          lock: true,
          text: '...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        
        // 
        const params = {
          status: this.filters.status || null,
          keyword: this.filters.keyword || null
        };
        
        // API
        const baseUrl = process.env.VUE_APP_API_URL || 'http://localhost:8080';
        const response = await axios({
          url: `${baseUrl}/api/admin/test-records/export`,
          method: 'GET',
          params,
          responseType: 'blob',
          headers: {
            'X-User-ID': localStorage.getItem('userId') || '1'
          }
        });
        
        // 
        const blob = new Blob([response.data], { 
          type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' 
        });
        
        // 
        let fileName = '.xlsx';
        const contentDisposition = response.headers['content-disposition'];
        if (contentDisposition) {
          const matches = contentDisposition.match(/filename="(.+?)"/);
          if (matches && matches.length > 1) {
            fileName = decodeURIComponent(matches[1]);
          }
        }
        
        // 
        const url = window.URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        link.download = fileName;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        window.URL.revokeObjectURL(url);
        
        loading.close();
        this.$message.success('');
      } catch (error) {
        console.error(':', error);
        this.$message.error(': ' + (error.message || ''));
      }
    },
    resetForm(formName) {
      if (this.$refs[formName]) {
        this.$refs[formName].resetFields();
      }
      this.addForm = {
        studentId: null,
        sportsItemId: null,
        score: 0,
        className: ''
      };
    },
    submitAdd() {
      this.$refs.addForm.validate(async (valid) => {
        if (valid) {
          try {
            const response = await this.$http.post('/api/admin/test-records', this.addForm);
            if (response.data.code === 200) {
              this.$message.success('');
              this.addDialog.visible = false;
              this.handleSearch(); // 
            } else {
              this.$message.error(response.data.msg || '');
            }
          } catch (error) {
            console.error(':', error);
            this.$message.error('');
          }
        }
      });
    },
    handleReview(row, action) {
      this.currentRecord = row;
      this.reviewForm.status = action;
      this.reviewDialog.visible = true;
    },
    submitReview() {
      this.$refs.reviewForm.validate(async (valid) => {
        if (valid) {
          try {
            // 
            const response = await this.$http.put(
              `/api/admin/records/${this.currentRecord.id}/review`, 
              null,
              {
                params: {
                  status: this.reviewForm.status,
                  comment: this.reviewForm.comment || ''
                }
              }
            );
            
            this.$message.success('');
            this.reviewDialog.visible = false;
            this.handleSearch();
          } catch (error) {
            // 
            if (error.response && error.response.data && 
                error.response.data.msg && 
                error.response.data.msg.includes('')) {
              
              // 
              this.$confirm(
                `${error.response.data.msg}`, 
                '', 
                {
                  confirmButtonText: '',
                  cancelButtonText: '',
                  type: 'warning'
                }
              ).then(async () => {
                // 
                const forceResponse = await this.$http.put(
                  `/api/admin/records/${this.currentRecord.id}/review`, 
                  null,
                  {
                    params: {
                      status: this.reviewForm.status,
                      comment: this.reviewForm.comment + ' ()',
                      forceApprove: true
                    }
                  }
                );
                
                this.$message.success('');
                this.reviewDialog.visible = false;
                this.handleSearch();
              }).catch(() => {
                this.$message.info('');
              });
            } else {
              // 
              console.error(':', error);
              this.$message.error(': ' + (error.response?.data?.msg || error.message || ''));
            }
          }
        }
      });
    },
    handleDetail(record) {
      this.currentRecord = record;
      this.detailDialog.visible = true;
    },
    handleSportsItemChange(value) {
      const item = this.sportsItems.find(i => i.id === value);
      if (item) {
        this.selectedItemUnit = item.unit;
      }
    },
    searchStudents(query) {
      if (query) {
        this.studentLoading = true;
        this.$http.get('/api/admin/students', {
          params: {
            query,
            page: 0,
            size: 10
          }
        }).then(response => {
          if (response.data.code === 200) {
            this.students = response.data.data.content || [];
          } else {
            this.$message.error(response.data.msg || '');
          }
        }).finally(() => {
          this.studentLoading = false;
        });
      }
    },
    handleSizeChange(newSize) {
      this.pageSize = newSize
      this.handleSearch()
    },
    handleCurrentChange(newPage) {
      this.currentPage = newPage
      this.handleSearch()
    },
    submitModify() {
      this.$refs.modifyForm.validate(async (valid) => {
        if (valid) {
          try {
            const response = await modifyReview(this.currentRecord.id, this.modifyForm)
            if (response.data.code === 200) {
              this.$message.success('')
              this.modifyDialog.visible = false
              this.handleSearch()
            } else {
              this.$message.error(response.data.msg || '')
            }
          } catch (error) {
            console.error(':', error)
            this.$message.error('')
          }
        }
      })
    },
    async loadSportsItems() {
      try {
        const response = await this.$http.get('/api/sports-items');
        if (response.data.code === 200) {
          this.sportsItems = response.data.data;
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      }
    },
    async loadClassNames() {
      try {
        const response = await this.$http.get('/api/admin/class-names');
        if (response.data.code === 200) {
          this.classNames = response.data.data;
        }
      } catch (error) {
        console.error(':', error);
      }
    },
  }
}
</script>

<style scoped>
.data-review {
  padding: 20px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.filters {
  margin-bottom: 20px;
}

.pagination-container {
  margin-top: 20px;
  text-align: right;
}
</style>