<template>
  <div class="test-records">
    <div class="filter-container">
      <el-select v-model="status" placeholder="" clearable>
        <el-option label="" value=""></el-option>
        <el-option label="" value="PENDING"></el-option>
        <el-option label="" value="APPROVED"></el-option>
        <el-option label="" value="REJECTED"></el-option>
      </el-select>
      <el-button type="primary" @click="getTestRecords"></el-button>
      <el-button @click="resetQuery"></el-button>
    </div>

    <el-table :data="records" border style="width: 100%">
      <el-table-column prop="createdAt" label="" width="180">
        <template slot-scope="scope">
          {{ formatDateTime(scope.row.createdAt) }}
        </template>
      </el-table-column>
      <el-table-column prop="sportsItemName" label=""></el-table-column>
      <el-table-column prop="score" label=""></el-table-column>
      <el-table-column prop="grade" label=""></el-table-column>
      <el-table-column prop="status" label="" width="120">
        <template slot-scope="scope">
          <el-tag :type="getStatusType(scope.row.status)">
            {{ getStatusText(scope.row.status) }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="reviewComment" label=""></el-table-column>
    </el-table>

    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[10, 20, 50]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
</template>

<script>
export default {
  name: 'TestRecords',
  data() {
    return {
      status: '',
      currentPage: 1,
      pageSize: 10,
      total: 0,
      records: [],
      loading: false
    }
  },
  created() {
    // 
    this.getTestRecords()
  },
  methods: {
    async getTestRecords() {
      try {
        this.loading = true
        const userId = localStorage.getItem('userId')
        
        // userId
        if (!userId) {
          this.$message.error('')
          this.$router.push('/login')
          return
        }
        
        // 
        console.log(':', {
          status: this.status,
          page: this.currentPage,
          size: this.pageSize,
          userId: userId
        })

        const response = await this.$axios.get('/api/student/test-records', {
          params: {
            status: this.status,
            page: this.currentPage,
            size: this.pageSize
          },
          headers: {
            userId: userId
          }
        })

        console.log(':', response.data.data.content)

        if (response.data.code === 200) {
          this.records = response.data.data.content
          this.total = response.data.data.totalElements
        } else {
          this.$message.error(response.data.message || '')
        }
      } catch (error) {
        console.error(':', error)
        if (typeof error === 'object' && error.message) {
          this.$message.error(`: ${error.message}`)
        } else {
          this.$message.error(`: ${error}`)
        }
      } finally {
        this.loading = false
      }
    },
    handleSizeChange(val) {
      console.log(':', val)
      this.pageSize = val
      this.getTestRecords()
    },
    handleCurrentChange(val) {
      console.log(':', val)
      this.currentPage = val
      this.getTestRecords()
    },
    resetQuery() {
      this.status = ''
      this.currentPage = 1
      this.getTestRecords()
    },
    getStatusType(status) {
      const typeMap = {
        'TESTED': 'success',
        'NOT_TESTED': 'info',
        'EXEMPTED': 'warning',
        'APPROVED': 'success',
        'REJECTED': 'danger',
        'PENDING': 'warning'
      };
      return typeMap[status] || 'info';
    },
    getStatusText(status) {
      const statusMap = {
        'TESTED': '',
        'NOT_TESTED': '',
        'EXEMPTED': '',
        'APPROVED': '',
        'REJECTED': '',
        'PENDING': ''
      };
      return statusMap[status] || status;
    },
    formatDateTime(datetime) {
      if (!datetime) return '';
      const date = new Date(datetime);
      return date.toLocaleString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit'
      });
    }
  }
}
</script>

<style scoped>
.test-records {
  padding: 20px;
}
.filter-container {
  margin-bottom: 20px;
}
.filter-container > * {
  margin-right: 10px;
}
</style> 