<template>
  <div class="sports-item-management">
    <div class="operation-bar">
      <div class="search-area">
        <el-input
          v-model="searchKeyword"
          placeholder=""
          style="width: 200px"
          @keyup.enter.native="handleSearch"
        >
          <el-button slot="append" icon="el-icon-search" @click="handleSearch"></el-button>
        </el-input>
      </div>
      
      <el-button type="primary" @click="handleAdd">
        <i class="el-icon-plus"></i> 
      </el-button>
    </div>

    <el-table
      :data="tableData"
      border
      stripe
      style="width: 100%"
      v-loading="loading"
    >
      <el-table-column prop="id" label="ID" width="80"></el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="type" label="" width="100"></el-table-column>
      <el-table-column prop="description" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="unit" label="" width="100"></el-table-column>
      <el-table-column prop="testMethod" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="location" label=""></el-table-column>
      <el-table-column prop="testTime" label="" width="120"></el-table-column>
      <el-table-column prop="standard" label="" show-overflow-tooltip></el-table-column>
      <el-table-column prop="isActive" label="" width="100">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.isActive"
            @change="handleStatusChange(scope.row)"
            active-color="#13ce66"
            inactive-color="#ff4949"
          >
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="" width="200">
        <template slot-scope="scope">
          <el-button-group>
            <el-button
              size="mini"
              type="primary"
              @click="handleEdit(scope.row)"
            >
              
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.row)"
            >
              
            </el-button>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <div class="pagination-container">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </div>

    <!-- / -->
    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="600px">
      <el-form :model="form" :rules="rules" ref="form" label-width="100px">
        <el-form-item label="" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="form.type" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input type="textarea" v-model="form.description" rows="3"></el-input>
        </el-form-item>
        <el-form-item label="" prop="unit">
          <el-input v-model="form.unit"></el-input>
        </el-form-item>
        <el-form-item label="" prop="testMethod">
          <el-input type="textarea" v-model="form.testMethod" rows="3"></el-input>
        </el-form-item>
        <el-form-item label="" prop="location">
          <el-input v-model="form.location"></el-input>
        </el-form-item>
        <el-form-item label="" prop="testTime">
          <el-input v-model="form.testTime" placeholder="3-4"></el-input>
        </el-form-item>
        <el-form-item label="" prop="standard">
          <el-input type="textarea" v-model="form.standard" rows="3" placeholder="7.08.5"></el-input>
        </el-form-item>
        <el-form-item label="" prop="isActive">
          <el-switch v-model="form.isActive"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="dialogVisible = false"></el-button>
        <el-button type="primary" @click="handleSubmit"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'SportsItemManagement',
  data() {
    return {
      searchKeyword: '',
      tableData: [],
      loading: false,
      currentPage: 1,
      pageSize: 10,
      total: 0,
      dialogVisible: false,
      dialogTitle: '',
      form: {
        name: '',
        type: '',
        description: '',
        unit: '',
        testMethod: '',
        location: '',
        testTime: '',
        standard: '',
        isActive: true
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '', trigger: 'change' }
        ],
        unit: [
          { required: true, message: '', trigger: 'blur' }
        ],
        testMethod: [
          { required: true, message: '', trigger: 'blur' }
        ],
        location: [
          { required: true, message: '', trigger: 'blur' }
        ],
        testTime: [
          { required: true, message: '', trigger: 'blur' }
        ],
        standard: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.fetchItemList()
  },
  methods: {
    async fetchItemList() {
      try {
        this.loading = true
        const response = await this.$http.get('/api/sports-items', {
          params: {
            keyword: this.searchKeyword,
            page: this.currentPage - 1,
            size: this.pageSize
          }
        })
        
        if (response.data.code === 200) {
          const data = response.data.data
          if (Array.isArray(data)) {
            this.tableData = data
            this.total = data.length
          } else if (data && data.content) {
            this.tableData = data.content
            this.total = data.totalElements
          }
          console.log(':', this.tableData)
        } else {
          this.$message.error(response.data.msg || '')
        }
      } catch (error) {
        console.error(':', error)
        this.$message.error('')
      } finally {
        this.loading = false
      }
    },
    handleSearch() {
      this.currentPage = 1
      this.fetchItemList()
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.fetchItemList()
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.fetchItemList()
    },
    handleAdd() {
      this.dialogTitle = ''
      this.form = {
        name: '',
        type: '',
        description: '',
        unit: '',
        testMethod: '',
        location: '',
        testTime: '',
        standard: '',
        isActive: true
      }
      this.dialogVisible = true
    },
    handleEdit(row) {
      this.dialogTitle = ''
      this.form = { ...row }
      this.dialogVisible = true
    },
    async handleSubmit() {
      this.$refs.form.validate(async valid => {
        if (valid) {
          try {
            const url = this.form.id ? 
              `/api/sports-items/${this.form.id}` : 
              '/api/sports-items'
            
            const method = this.form.id ? 'put' : 'post'
            const response = await this.$http[method](url, this.form)
            
            if (response.data.code === 200) {
              this.$message.success(this.form.id ? '' : '')
              this.dialogVisible = false
              this.fetchItemList()
            } else {
              this.$message.error(response.data.msg || '')
            }
          } catch (error) {
            console.error(':', error)
            this.$message.error('')
          }
        }
      })
    },
    async handleDelete(row) {
      try {
        await this.$confirm('', '', {
          type: 'warning'
        })
        const response = await this.$http.delete(`/api/sports-items/${row.id}`)
        if (response.data.code === 200) {
          this.$message.success('')
          this.fetchItemList()
        } else {
          this.$message.error(response.data.msg || '')
        }
      } catch (error) {
        if (error !== 'cancel') {
          console.error(':', error)
          this.$message.error('')
        }
      }
    },
    async handleStatusChange(row) {
      try {
        const response = await this.$http.put(`/api/sports-items/${row.id}/status`, {
          isActive: row.isActive
        })
        if (response.data.code === 200) {
          this.$message.success(`${row.isActive ? '' : ''}`)
        } else {
          row.isActive = !row.isActive // 
          this.$message.error(response.data.msg || '')
        }
      } catch (error) {
        row.isActive = !row.isActive // 
        console.error(':', error)
        this.$message.error('')
      }
    }
  }
}
</script>

<style scoped>
.sports-item-management {
  padding: 20px;
}
.operation-bar {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.search-area {
  display: flex;
  align-items: center;
}
.pagination-container {
  margin-top: 20px;
  text-align: right;
}
</style> 