<template>
  <div class="notice-management">
    <div class="operation-bar">
      <div class="search-area">
        <el-input
          v-model="searchKeyword"
          placeholder=""
          style="width: 200px"
          @keyup.enter.native="handleSearch"
        >
          <el-button slot="append" icon="el-icon-search" @click="handleSearch"></el-button>
        </el-input>
        <el-select v-model="searchType" placeholder="" style="width: 120px; margin-left: 10px">
          <el-option label="" value=""></el-option>
          <el-option label="" value="TEST_SCHEDULE"></el-option>
          <el-option label="" value="SCORE_RELEASE"></el-option>
          <el-option label="" value="SYSTEM_MAINTENANCE"></el-option>
          <el-option label="" value="OTHER"></el-option>
        </el-select>
      </div>
      
      <el-button type="primary" @click="handleAdd">
        <i class="el-icon-plus"></i> 
      </el-button>
    </div>

    <el-table
      :data="noticeList"
      border
      stripe
      style="width: 100%"
      v-loading="loading"
    >
      <el-table-column prop="id" label="ID" width="80"></el-table-column>
      <el-table-column prop="type" label="" width="100">
        <template slot-scope="scope">
          <el-tag :type="getTypeTagType(scope.row.type)">
            {{ getTypeLabel(scope.row.type) }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="title" label="" show-overflow-tooltip>
        <template slot-scope="scope">
          <span :class="{'important-notice': scope.row.priority === 'HIGH'}">
            {{ scope.row.title }}
          </span>
        </template>
      </el-table-column>
      <el-table-column prop="createTime" label="" width="180">
        <template slot-scope="scope">
          {{ formatDate(scope.row.createTime) }}
        </template>
      </el-table-column>
      <el-table-column prop="status" label="" width="100">
        <template slot-scope="scope">
          <el-switch
            v-model="scope.row.status"
            :active-value="1"
            :inactive-value="0"
            @change="handleStatusChange(scope.row)"
            active-color="#13ce66"
            inactive-color="#ff4949"
          >
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="" width="200">
        <template slot-scope="scope">
          <el-button-group class="operation-group">
            <el-button
              type="primary"
              size="mini"
              @click="handleView(scope.row)">
              
            </el-button>
            <el-button
              type="warning"
              size="mini"
              @click="handleEdit(scope.row)">
              
            </el-button>
            <el-button
              type="danger"
              size="mini"
              @click="handleDelete(scope.row)">
              
            </el-button>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <div class="pagination-container">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </div>

    <!-- / -->
    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="700px">
      <el-form :model="form" :rules="rules" ref="form" label-width="80px">
        <el-form-item label="" prop="type">
          <el-select v-model="form.type" placeholder="" style="width: 100%">
            <el-option label="" value="TEST_SCHEDULE"></el-option>
            <el-option label="" value="SCORE_RELEASE"></el-option>
            <el-option label="" value="SYSTEM_MAINTENANCE"></el-option>
            <el-option label="" value="OTHER"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="priority">
          <el-select v-model="form.priority" placeholder="" style="width: 100%">
            <el-option label="" value="NORMAL"></el-option>
            <el-option label="" value="HIGH"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="title">
          <el-input v-model="form.title" maxlength="100" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <el-input
            type="textarea"
            v-model="form.content"
            :rows="10"
            maxlength="2000"
            show-word-limit
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-switch
            v-model="form.status"
            :active-value="1"
            :inactive-value="0"
            active-text=""
            inactive-text=""
          >
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="dialogVisible = false"></el-button>
        <el-button type="primary" @click="handleSubmit"></el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="viewDialogVisible" width="700px">
      <div class="notice-header">
        <el-tag :type="getTypeTagType(currentNotice.type)" class="notice-type">
          {{ getTypeLabel(currentNotice.type) }}
        </el-tag>
        <h3 class="notice-title">{{ currentNotice.title }}</h3>
      </div>
      <div class="notice-meta">
        <span>{{ formatDate(currentNotice.createTime) }}</span>
        <span v-if="currentNotice.priority === 'HIGH'" class="notice-priority">
          <i class="el-icon-warning"></i> 
        </span>
      </div>
      <div class="notice-content">{{ currentNotice.content }}</div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'NoticeManagement',
  data() {
    return {
      searchKeyword: '',
      searchType: '',
      noticeList: [],
      loading: false,
      dialogVisible: false,
      viewDialogVisible: false,
      dialogTitle: '',
      currentNotice: {},
      currentPage: 1,
      pageSize: 10,
      total: 0,
      form: {
        id: null,
        type: 'OTHER',
        priority: 'NORMAL',
        title: '',
        content: '',
        status: 1,
        createTime: null,
        updateTime: null,
        createBy: null
      },
      rules: {
        type: [
          { required: true, message: '', trigger: 'change' }
        ],
        priority: [
          { required: true, message: '', trigger: 'change' }
        ],
        title: [
          { required: true, message: '', trigger: 'blur' },
          { max: 100, message: '100', trigger: 'blur' }
        ],
        content: [
          { required: true, message: '', trigger: 'blur' },
          { max: 2000, message: '2000', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    const userId = localStorage.getItem('userId')
    if (userId) {
      this.form.createBy = parseInt(userId)
    }
    this.fetchNoticeList()
  },
  methods: {
    formatDate(date) {
      if (!date) return ''
      return new Date(date).toLocaleString()
    },
    getTypeTagType(type) {
      const typeMap = {
        'TEST_SCHEDULE': 'primary',
        'SCORE_RELEASE': 'success',
        'SYSTEM_MAINTENANCE': 'warning',
        'OTHER': 'info'
      }
      return typeMap[type] || 'info'
    },
    getTypeLabel(type) {
      const typeMap = {
        'TEST_SCHEDULE': '',
        'SCORE_RELEASE': '',
        'SYSTEM_MAINTENANCE': '',
        'OTHER': ''
      }
      return typeMap[type] || ''
    },
    async fetchNoticeList() {
      try {
        this.loading = true
        console.log('Fetching notices...')
        const res = await this.$http.get('/api/admin/notices', {
          params: {
            keyword: this.searchKeyword,
            type: this.searchType,
            page: this.currentPage - 1,
            size: this.pageSize
          }
        })
        
        if (res.data.code === 200) {
          const data = res.data.data
          this.noticeList = Array.isArray(data) ? data : (data.content || [])
          this.total = data.totalElements || this.noticeList.length
        } else {
          this.$message.error(res.data.msg || '')
        }
      } catch (error) {
        console.error(':', error)
        this.$message.error('')
      } finally {
        this.loading = false
      }
    },
    handleSearch() {
      this.currentPage = 1
      this.fetchNoticeList()
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.fetchNoticeList()
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.fetchNoticeList()
    },
    handleAdd() {
      this.dialogTitle = ''
      this.form = {
        id: null,
        type: 'OTHER',
        priority: 'NORMAL',
        title: '',
        content: '',
        status: 1,
        createTime: null,
        updateTime: null,
        createBy: null
      }
      
      // ID
      if (this.$store.state.user) {
        this.form.createBy = this.$store.state.user.id
      } else {
        // localStorage
        const userId = localStorage.getItem('userId')
        if (userId) {
          this.form.createBy = parseInt(userId)
        }
      }
      
      this.dialogVisible = true
    },
    handleView(row) {
      this.currentNotice = { ...row }
      this.viewDialogVisible = true
    },
    handleEdit(row) {
      this.dialogTitle = ''
      this.form = { ...row }
      this.dialogVisible = true
    },
    async handleSubmit() {
      this.$refs.form.validate(async valid => {
        if (valid) {
          try {
            const url = this.form.id ? `/api/admin/notices/${this.form.id}` : '/api/admin/notices'
            const method = this.form.id ? 'put' : 'post'
            
            const res = await this.$http[method](url, this.form)
            if (res.data.code === 200) {
              this.$message.success(this.form.id ? '' : '')
              this.dialogVisible = false
              this.fetchNoticeList()
            } else {
              this.$message.error(res.data.msg || '')
            }
          } catch (error) {
            console.error(':', error)
            this.$message.error(error.response?.data?.msg || '')
          }
        }
      })
    },
    async handleDelete(row) {
      try {
        await this.$confirm('', '', {
          type: 'warning'
        })
        const res = await this.$http.delete(`/api/admin/notices/${row.id}`)
        if (res.data.code === 200) {
          this.$message.success('')
          this.fetchNoticeList()
        }
      } catch (error) {
        if (error !== 'cancel') {
          this.$message.error('')
        }
      }
    },
    async handleStatusChange(row) {
      try {
        const res = await this.$http.put(`/admin/notices/${row.id}/status`, {
          status: row.status
        })
        if (res.data.code === 200) {
          this.$message.success(`${row.status === 1 ? '' : ''}`)
        } else {
          row.status = row.status === 1 ? 0 : 1 // 
          this.$message.error(res.data.msg || '')
        }
      } catch (error) {
        row.status = row.status === 1 ? 0 : 1 // 
        this.$message.error('')
      }
    }
  }
}
</script>

