  margin-top: 20px;
  text-align: right;
}
.notice-header {
  text-align: center;
  margin-bottom: 20px;
}
.notice-type {
  margin-bottom: 10px;
}
.notice-title {
  margin: 10px 0;
  font-size: 20px;
}
.notice-meta {
  color: #666;
  margin-bottom: 20px;
  text-align: center;
  display: flex;
  justify-content: center;
  gap: 20px;
}
.notice-priority {
  color: #E6A23C;
}
.notice-content {
  line-height: 1.8;
  white-space: pre-wrap;
